import { Component, OnInit } from '@angular/core';
import { FormBuilder,FormGroup,Validators } from '@angular/forms';
import  {ReactiveFormsModule} from '@angular/forms';
import {Router,ActivatedRoute}  from '@angular/router';
import {OperationService}  from '../operation.service';

@Component({
  selector: 'app-useredit',
  templateUrl: './useredit.component.html',
  styleUrls: ['./useredit.component.scss']
})
export class UsereditComponent implements OnInit {
    user:FormGroup;
    x: any;
    model: any = {};
    submitted = false;
    id:any;
    title:string;
  constructor(private fb : FormBuilder,private active:ActivatedRoute,private operation:OperationService,private router:Router) { }


update(){
      
     //insert and update user

      if(this.id==undefined){

        this.submitted = true;
        if (this.user.invalid) {
           
            return;
        }else{
       this.operation.insertdata(this.user.value,"user_reg")
       this.router.navigate(['user'])
            }

      }else{
         this.submitted = true;

        // stop here if form is invalid
        if (this.user.invalid) {
           
            return;
        }else{
          this.operation.updatedata(this.user.value,'user_update');
          
          this.router.navigate(['user']);
        }
        
        console.log(this.user.invalid)
        alert('SUCCESS!! :-)')
   
        
      }
}





  ngOnInit() {

this.active.params.subscribe(params=>{
       this.id=params['id'];
       console.log(this.id)

         if(this.id==undefined){
              this.title="Add User";
           }
           else{
             this.title="Update User"
             this.operation.Edit(params['id'],"edituser").subscribe(res=>{
           this.x=res[0];
             // put values in the form
             console.log(res)
      this.user.setValue({
      user_id:this.x.user_id,
      email:this.x.email,
      mobile:this.x.mobile,
      name:this.x.name,
      roles:this.x.roles,
      mobile_varified:this.x.mobile_varified,
      quick_links:this.x.quick_links


      
                });
         })
           }
  });



  	  	this.user=this.fb.group(
  	{
  		
     
   
   user_id:[''],
email:['',[Validators.required, Validators.email]],
mobile:['',[Validators.required]],
name:['',Validators.required],
roles:['',Validators.required],
mobile_varified:['',Validators.required],
quick_links:['',Validators.required]

  	});
  }

   get f() { return this.user.controls; }


}
